import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import {
  PaymentOptionsService,
  PaymentOrderHttpService,
  PayordPaymentErrorsConfigProvider,
  Routes,
} from '@backbase/payment-orders-ang';
import {
  CreatePaymentFormComponent,
  CreatePaymentFormModule,
  CreatePaymentService,
  ReviewPaymentContainerComponent,
  ReviewPaymentModule,
} from '@backbase/payments-collection-ui-ang';
import { PayordFormBuilderAngModule } from '@backbase/payord-form-builder-ang';

import { InitiatePaymentJourneyComponent } from './initiate-payment-journey.component';

export const InitiatePaymentDefaultRoute: Route = {
  path: '',
  component: InitiatePaymentJourneyComponent,
  children: [
    { path: '', redirectTo: Routes.FORM, pathMatch: 'full' },
    { path: Routes.FORM, component: CreatePaymentFormComponent },
    { path: Routes.REVIEW, component: ReviewPaymentContainerComponent },
  ],
};

@NgModule({
  declarations: [InitiatePaymentJourneyComponent],
  imports: [
    CommonModule,
    RouterModule,
    ReviewPaymentModule,
    CreatePaymentFormModule,
    PayordFormBuilderAngModule,
    BackbaseCoreModule.withConfig({
      classMap: {
        InitiatePaymentJourneyComponent,
      },
    }),
  ],
  providers: [
    CreatePaymentService,
    PaymentOptionsService,
    PaymentOrderHttpService,
    PayordPaymentErrorsConfigProvider,
  ],
})
export class InitiatePaymentJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = {
      route: InitiatePaymentDefaultRoute,
    }
  ): ModuleWithProviders<InitiatePaymentJourneyModule> {
    return {
      ngModule: InitiatePaymentJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
